import React, { useState, useEffect, useCallback } from 'react'

import { useNavigate, useParams } from 'react-router'
import "./movie-grid.scss"
import Input from "../input/Input"


import MovieCard from '../movie-card/MovieCard'
import tmdbApi, { category, movieType, tvType } from '../../api/tmdbApi'
import Button, { OutLineButton } from '../button/Button'

const MovieGrid = props => {
    

    const [items, setItems] = useState([])

    const [page, setPage] = useState(1);

    const [totalPage, setTotalPage] = useState(0);

    const { keyword } = useParams()
    const {type} = useParams();

    
    


    useEffect(() => {
        const getList = async () => {
            let response = null
            

            if (keyword === undefined) {
                const params = {}
                switch (props.category) {
                    case category.movie:
                        response = await tmdbApi.getMoviesList( type , { params });
                        break;
                    default:
                        response = await tmdbApi.getTvList(type, { params });
                }
            } else {
                const params = {
                    query: keyword
                }
                response = await tmdbApi.search(props.category, { params })
            }
            setItems(response.results);
            setTotalPage(response.total_pages);
        }
        getList();
    }, [props.category, keyword, type])
    
    const loadMore =  async () => {
        let response = null;
        if (keyword === undefined) {
            const params = {
                page: page+1
            }
            switch (props.category) {
                case category.movie:
                    response = await tmdbApi.getMoviesList(movieType.upcoming, { params });
                    break;
                default:
                    response = await tmdbApi.getTvList(tvType.popular, { params });
            }
        } else {
            const params = {
                page: page+1,
                query: keyword
            }
            response = await tmdbApi.search(props.category, { params })
        }
        setItems([...items,...response.results]);
        setPage(page+1);

    }

    return (
        <>
            <div className="section mb-3">
                <MovieSearch category={props.category} keyword={keyword} type={type}/>
            </div>
            <div className='movie-grid'>
                {
                    items.map((item, i) => <MovieCard category={props.category} item={item} key={i} />)
                }
            </div>
            {
                page < totalPage ? (
                    <div className="movie-grid__loadmore">
                        <OutLineButton className="small" onClick = {loadMore}>Load more</OutLineButton>
                    </div>
                ) : null
            }
        </>
    )
}
export const MovieSearch = props => {

    const history = useNavigate();
    const [keyword, setKeyword] = useState(props.keyword?props.keyword:'');
    const goToSearch = useCallback(
        () => {
            if(keyword.trim().length > 0){
                history(`/${category[props.category]}/search/${keyword}`);
            }else if(keyword===''){
                history(`/${category[props.category]}/popular`);
            }
        },
        [keyword, props.category, history]
    );

    useEffect(() => {
        const enterEvent = (e) => {
            e.preventDefault();
            if (e.keyCode === 13 &&keyword!=='') {
                goToSearch();
            }
        }
        document.addEventListener('keyup', enterEvent);
        return () => {
            document.removeEventListener('keyup', enterEvent);
        };
    }, [keyword, goToSearch]);
    return (
        <div className="movie-search">
            <Input 
                type="text"
                placeholder="Enter keyword"
                value = {keyword}
                onChange={(e) => {setKeyword(e.target.value); goToSearch()}}
            />
            <Button className="small" onClick = {goToSearch}>Search</Button>
        </div>
    )
}

export default MovieGrid